import React from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import * as lendingCriteriaActions from "../../actions/lendingCriteriaActions";
import {getBanksList} from "../../actions/reportsActions";
import LendingCriteriaAdd from "./LendingCriteriaAdd";
import Loader from "../common/Loader";

class LendingCriteriaAddContainer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      user : JSON.parse(localStorage.getItem("data"))
    };
  }
  componentDidMount() {
    this.props.getBanks(this.state.user.bank);
    this.props.actions.getLendingTypes();
    this.props.actions.getPropertyTypes();
    this.props.actions.getLendingCriteriaLists();
    this.props.actions.getRecourseTypes();
    this.props.actions.getStateList();
  }

  render() {
    const {
      stateList,
      lendingTypes,
      propTypesList,
      searchResults,
      maxTermTypes,
      minDscrTypes,
      maxInterestOnlyTypes,
      prePaymentPenaltyTypes,
      maxInitialTermTypes,
      maxPermAfterConstructionTypes,
      maxAmortizationTypes,
      recourseTypes,
      simpleRateSheetTypes,
      loading,
      currentBank,
      currentBanker,
      banksBankers
    } = this.props.lendingCriteria;
    return (
      <div>
        {lendingTypes &&
        lendingTypes.length &&
        propTypesList &&
        propTypesList.length &&
        this.props.reports.banks &&
        this.props.reports.banks.length &&
        stateList &&
        stateList.length ? (
          <LendingCriteriaAdd
            isQTS={this.state.user.is_qts}
            loading={loading}
            searchResults={searchResults}
            bankName={
              this.state.user.is_lender &&
              this.state.user.bank.bank_name
            }
            banks={this.props.reports.banks}
            banksBankers={banksBankers}
            currentBank={currentBank}
            currentBanker={currentBanker}
            setCurrentBank={this.props.actions.setCurrentBank}
            setCurrentBanker={this.props.actions.setCurrentBanker}
            getBanksBankers={this.props.actions.getBanksBankers}
            getLendingCriteriaByBank={this.props.actions.getParameterListByBank}
            submitAddLendingCriteriaForm={this.props.actions.addLendingCriteria}
            lendingTypes={lendingTypes}
            propTypesList={propTypesList}
            stateList={stateList}
            searchCityOrZip={this.props.actions.searchCityOrZip}
            lists={{
              maxTermTypes,
              minDscrTypes,
              maxInterestOnlyTypes,
              prePaymentPenaltyTypes,
              maxInitialTermTypes,
              lendingTypes,
              maxPermAfterConstructionTypes,
              maxAmortizationTypes,
              recourseTypes,
              simpleRateSheetTypes,
            }}
          />
        ) : (
          <Loader />
        )}
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    lendingCriteria: state.lendingCriteria,
    reports: state.reports,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(lendingCriteriaActions, dispatch),
    getBanks: bindActionCreators(getBanksList, dispatch),
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(LendingCriteriaAddContainer);
